import { WithdrawOneData } from "../types";

export const bybitWithdraw = async (data: WithdrawOneData): Promise<void> => {
  try {
    // @ts-ignore
    const bybit = new ccxt.bybit({
      apiKey: data.apiKey,
      secret: data.secret,
    });

    const result = await bybit.withdraw(
      data.token,
      data.amount,
      data.wallet,
      null,
      {
        forceChain: 1,
        network: data.network,
      }
    );

    data.setStatus(
      (prev: string) =>
        `${prev}>>> ${result.status} | ${data.wallet} | ${data.amount}\n`
    );
  } catch (error: any) {
    data.setStatus(
      (prev: string) =>
        `${prev}>>> Fail | ${data.wallet} | error: ${error.message}\n`
    );
  }
};
