import { WithdrawOneData } from "../types";

export const okxWithdraw = async (data: WithdrawOneData): Promise<void> => {
  try {
    // @ts-ignore
    const okx = new ccxt.okx({
      apiKey: data.apiKey,
      secret: data.secret,
      password: data.passPhrase,
      enableRateLimit: true,
    });

    const chainName = `${data.token}-${data.network}`;

    const result = await okx.withdraw(data.token, data.amount, data.wallet, {
      toAddress: data.wallet,
      chainName,
      dest: 4,
      fee: getWithdrawalFee(okx, data.token, chainName),
      pwd: "-",
      amt: data.amount,
      network: data.network,
    });

    data.setStatus(
      (prev: string) =>
        `${prev}>>> ${result.status} | ${data.wallet} | ${data.amount}\n`
    );
  } catch (error: any) {
    data.setStatus(
      (prev: string) =>
        `${prev}>>> Fail | ${data.wallet} | error: ${error.message}\n`
    );
  }
};

const getWithdrawalFee = async (okx: any, token: string, chainName: string) => {
  const currencies = await okx.fetchCurrencies();

  for (const currencySymbol in currencies) {
    if (currencySymbol === token) {
      const currencyInfo = currencies[currencySymbol];
      const networkInfo = currencyInfo.networks;
      if (networkInfo) {
        for (const network in networkInfo) {
          const networkData = networkInfo[network];
          const networkId = networkData.id;
          if (networkId === chainName) {
            const withdrawalFee = currencyInfo.networks[network].fee;
            if (withdrawalFee === 0) {
              return 0;
            } else {
              return withdrawalFee;
            }
          }
        }
      }
    }
  }
};
